*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font: normal 1rem/1.6 'Open Sans', arial, sans-serif;
  color: #000;
}
h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: 'Roboto Condensed', arial, sans-serif;
  font-weight: 700;
  color: #009bd6;
  line-height: 1.2;
  margin-bottom: 0.5em;
}
h1,
.h1 {
  font-size: 4.25rem;
}
h2,
.h2 {
  font-size: 3.125rem;
}
h3,
.h3 {
  font-size: 2.375rem;
}
h4,
.h4 {
  font-size: 1.75rem;
}
h5,
.h5 {
  font-size: 1.25rem;
}
h6,
.h6 {
  font-size: 1.125rem;
}
a {
  color: #009bd6;
  text-decoration: none;
  transition: all 250ms ease;
}
a:hover {
  text-decoration: underline;
}
a img {
  border: 0;
}
img {
  max-width: 100%;
  height: auto !important;
}
p {
  margin: 20px 0;
}
ol,
ul {
  padding-left: 20px;
}
ol li + li,
ul li + li {
  margin-top: 0.5em;
}
ol li::marker,
ul li::marker {
  color: #009bd6;
  font-weight: 700;
}
dd {
  margin: 20px;
}
hr {
  margin: 30px 0;
  border-color: #f3f3f3;
}
pre,
blockquote {
  margin-top: 2em;
  margin-bottom: 2em;
  padding: 0.5em 1.5em;
  border-left: 2px solid #009bd6;
  font-size: 1.333rem;
}
*:first-child {
  margin-top: 0;
}
*:last-child {
  margin-bottom: 0;
}
.mceTemp + * {
  margin-top: 0;
}
.alignleft {
  display: block;
  float: left;
  margin-right: 30px;
}
.alignright {
  display: block;
  float: right;
  margin-left: 30px;
}
.aligncenter {
  display: block;
  margin: 20px auto;
  clear: both;
}
.wp-caption-dd {
  padding: 0;
  margin: 5px 0 0;
  text-align: center;
  font-size: -3rem;
}
